export function getRisk(tokenSecurity) {
	const riskList = new Array()

	function add(arr, input) {
		if (input === undefined | input === null) return
		riskList.push(arr[input])
	}

	add([
			['合约未开源，可能存在漏洞机制', '凶'],
			['合约已开源', '吉'],
			// ['合约开源未知', '凶']
		],
		tokenSecurity.is_open_source)

	add([
			['看起来不像代理合约', '吉'],
			['是代理合约，可能存在潜在升级替换风险', '凶'],
			// ['无法确定合约的代理', '凶']
		],
		tokenSecurity.is_proxy)

	add([
			['不能增发', '吉'],
			['可增发，存在价格暴跌风险', '凶'],
			// ['无法确认增发权限', '中']
		],
		tokenSecurity.is_mintable)

	add([
			['不能找回管理员权限', '吉'],
			['可收回管理员权限', '凶'],
			// ['无法确认管理员权限', '凶']
		],
		tokenSecurity.can_take_back_ownership)

	add([
			['所有者不能篡改余额', '吉'],
			['可修改余额，导致风险', '凶'],
		],
		tokenSecurity.owner_change_balance)

	add([
			['无隐藏用户', '吉'],
			['有隐藏用户，所有权未被放弃', '凶'],
		],
		tokenSecurity.hidden_owner)

	// add([
	// 		['能出售全部', '中'],
	// 		['不能出售全部', '中'],
	// 	],
	// 	tokenSecurity.cannot_sell_all)	

	add([
			['滑点费率不可修改', '吉'],
			['滑点费率可修改，存在风险', '凶'],
		],
		tokenSecurity.slippage_modifiable)

	// add([
	// 		['无蜜罐', '吉'],
	// 		['有蜜罐，高风险，是骗局', '凶'],
	// 	],
	// 	tokenSecurity.is_honeypot)

	add([
			['未检测到交易暂停方法', '吉'],
			['随时暂停交易风险，无法出售', '合'],
		],
		tokenSecurity.transfer_pausable)

	add([
			['因权限转让，无法设置黑名单', '吉'],
			['存在黑名单，有被停止交易风险', '合'],
		],
		tokenSecurity.is_blacklisted)

	add([
			['不存在白名单', '吉'],
			['存在白名单，有特权交易', '合'],
		],
		tokenSecurity.is_whitelisted)

	// add([
	// 		['可在Dex交易', '合'],
	// 		['不可在Dex交易', '合'],
	// 	],
	// 	tokenSecurity.is_in_dex)	

	// add([
	// 		['没有大额交易及持有', '合'],
	// 		['有大额交易及持有', '合'],
	// 	],
	// 	tokenSecurity.is_anti_whale)	

	add([
			['无交易冷却机制', '吉'],
			['有交易冷却', '合'],
		],
		tokenSecurity.trading_cooldown)

	// add([
	// 		['', '吉'],
	// 		['', '凶'],
	// 	],
	// 	tokenSecurity.)	

	// riskList.sort(function(v1, v2) {
	// 		if ((v1[1] === '合' && v2[1] === '吉') || (v1[1] === '凶' && v2[1] === '吉') || (v1[1] === '凶' &&
	// 				v2[1] === '合')) return -1
	// 			else if ((v1[1] === '吉' && v2[1] === '合') || (v1[1] === '吉' && v2[1] === '凶') || (v1[1] ===
	// 					'合' && v2[1] === '凶'))
	// 				return 1
	// 			else return 0
	// 		})
	
	// console.log('合'>'凶');
	riskList.sort(function(v1, v2) {
		if (v1[1] < v2[1])
			return -1
		else if (v1[1] > v2[1])
			return 1
		else 0
	})

	return riskList
}
