<template>
	<div id="app">
		<el-row :gutter="10">
			<el-col :xs="24" :sm="24" :md="10">
				<div class="hard">
					注：我们可帮助您确定智能合约可能是一个骗局，但没有100%的担保。我们会尽最大的努力检测您遇到的骗局。BFF-DIT合约检测为用户提供参考依据，查询合约详情规范以及风险安全等。
				</div>
				<div class="contract">
					<el-input v-model="input" placeholder="请输入BSC合约地址">
					</el-input>
					<el-button class="button" @click="check">
						<i v-if="is_send" class="el-icon-loading"></i>
						审计
					</el-button>
					<div class="message">
						@检测结果仅代表链上数据分析值，不作为任何投资建议或担保
					</div>
				</div>

				<div class="dashboard">
					<el-progress class="main" type="dashboard" :percentage="0" color="var(--cl-color)"
						:stroke-width="20" :width="200" :show-text="true" :format='()=>"风险评估"'></el-progress>
					<div class="bottom">风险评估中</div>
				</div>
				<div class="community">
					<div class="title">社区信任</div>
					<el-progress class="table" :text-inside="true" :stroke-width="16" :percentage="0" color="#15b886">
					</el-progress>
					<div class="total">(45次投票)</div>
					<div class="remark">备注: 合约检测只是单方面检测，某些新貔貅可能未能检测到。需要结合更多维度来分析，比如K线走势，持仓变化，资金流向等等</div>
				</div>
			</el-col>
			<el-col v-if="is_init" :xs="24" :sm="24" :md="14">
				<el-empty description="等待检测中"></el-empty>
			</el-col>
			<div v-else>
				<el-col :xs="24" :sm="24" :md="7">
					<div class="basics">
						基本信息
						<div class="table" v-for="(item,index) in basic" :key="index">
							<div class="left">{{item[0]}}</div>
							<div class="right">{{item[1]}}</div>
						</div>
					</div>
					<div class="basics">
						风险检查
						<div class="logic" v-for="(item,index) in risk" :key="index">
							<div class="left">
								<i v-if="item[1]==='吉'" class="el-icon-success"
									style="color:#36c882;font-size: 18px;"></i>
								<i v-else-if="item[1]==='合'" class="el-icon-warning"
									style="color: #fcad10;font-size: 18px;"></i>
								<i v-else class="el-icon-message-solid" style="color: #fc1010;font-size: 18px;"></i>
								<div>{{item[0]}}</div>
							</div>
						</div>
					</div>
					<div class="basics">
						交易&流动性
						<div class="table" v-for="(item,index) in deal" :key="index">
							<div class="left">{{item[0]}}</div>
							<div class="right">{{item[1]}}</div>
						</div>
					</div>
				</el-col>
				<el-col :xs="24" :sm="24" :md="7">
					<div class="basics">
						持币信息
						<div class="cash">
							<div>持币人数:{{tokenSecurity.holder_count.valueOf()}}</div>
						</div>
						<div class="cash">
							<div>Top 10 持币占比</div>
							<div>
								<!-- <el-progress :percentage="50"></el-progress> -->
								{{totalHolders}}%
							</div>
						</div>
						<div class="table" v-for="(item,index) in tokenSecurity.holders" :key="index">
							<div class="left">{{index+1}}.
								<div v-if="item.is_locked" class="el-icon-lock"
									style="color:#fcad10;font-size: 12px;fill: #fcad10;fill-rule:inherit;">
								</div>
								<div v-if="item.is_locked" style="color:#767f8f">Lock Contract</div>
								<div v-if="item.is_contract" class="el-icon-document" style="font-size: 12px;">
									PancakeV2</div>
								<div v-if="!(item.is_locked || item.is_contract)">
									{{item.address}}
								</div>
							</div>
							<div class="right">
								{{item.balance}}({{item.percent}})%
							</div>
						</div>
					</div>

					<div class="basics">
						池子检查
						<div class="cash">
							<div>LP 持币人数:{{tokenSecurity.lp_holder_count}}</div>
						</div>
						<div class="cash">
							<div>LP锁仓占比</div>
							<div>
								<!-- <el-progress :percentage="50"></el-progress> -->
								{{lp_holders}}%
							</div>
						</div>
						<div class="table" v-for="(item,index) in tokenSecurity.lp_holders" :key="index">
							<div class="left">{{index+1}}.
								<div v-if="item.is_locked" class="el-icon-lock" style="color:#fcad10;font-size: 12px;">
								</div>
								<div v-if="item.is_locked" style="color:#767f8f">Lock Contract</div>
								<div v-if="item.is_contract" class="el-icon-document" style="font-size: 12px;">
									PancakeV2</div>
								<div v-if="!(item.is_locked || item.is_contract)">
									{{item.address}}
								</div>
							</div>
							<div class="right">
								{{item.balance}}({{item.percent}})%
							</div>
						</div>
					</div>
				</el-col>
			</div>
		</el-row>
	</div>
</template>

<script>
	import { getCheck, gettest } from '../api/goplus.js'
	import { getRisk } from "../api/checkRisk.js"

	export default {
		name: 'app',
		computed: {
			//基本信息
			basic() {
				return [
					['Token 合约地址', this.input],
					['总量', this.tokenSecurity.total_supply],
					['流通总量', '-'],
					['精度', '']
				]
			},
			//交易%流动性
			deal() {
				return [
					['买入费', this.tokenSecurity.buy_tax * 100 + '%'],
					['买出费', this.tokenSecurity.sell_tax * 100 + '%'],
					// ['跃(交易)地址', '234'],
					// ['4h交易数', '1234']
				]
			},
			//风险检查		
			risk(){
				return getRisk(this.tokenSecurity)
			}
		},
		watch: {
			tokenSecurity(val) {
				this.totalHolders = this.changeDate(val.holders)
				this.totalHolders = Math.floor(this.totalHolders * 100) / 100
				this.lp_holders = this.changeDate(val.lp_holders)
				this.lp_holders = Math.floor(this.lp_holders) * 100 / 100
			}
		},
		data() {
			return {
				input: '',
				//持币人数
				holders: [],
				//持币人数百分比
				totalHolders: 0,
				//lp持币人数
				lp_holders: [],
				//lp持币人数百分比
				totalLp: 0,
				//审计发送请求
				is_send: false,
				//初始化函数
				is_init: true,
				//tokenSecurity API返回值
				tokenSecurity: {},

			}
		},

		methods: {
			//排名数据整理
			changeDate(arr) {
				if(arr===undefined) return new Array()
				let total = 0;
				arr.forEach((ele, index, array) => {
					ele.address = ele.address.slice(0, 2) + '...' + ele.address.slice(-4)
					ele.balance = Math.round(ele.balance * 100) / 100
					ele.percent = Math.round(ele.percent * 10000) / 100
					total += ele.percent
				})
				return total
			},
			//客户点击审计事件
			check() {
				this.is_send = true
				this.is_init = true
				getCheck(this.input).then(res => {
					res.text().then(re => {
						this.is_send = false
						this.is_init = false
						this.tokenSecurity = JSON.parse(re).result[this.input.toLocaleLowerCase()];
						console.log(this.tokenSecurity);
					})
				})
				// gettest().res(res=>{
				// 	res.text().then(result=>{
				// 		const addressSecutity= JSON.parse(result)
				// 		console.log(addressSecutity);
				// 	})

				// })
			}
		}

	}
</script>

<style lang="scss">
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		--cl-color: #f8be46;
		--cl-back: aliceblue;
		--cl-hard: #f8fff8;
		--cl-table: #f5f8ff;
		--cl-main: #538bed;
		--cl-font: #141723;
		background-color: var(--cl-back);
		color: var(--cl-color);
	}

	.el-col {
		background-color: var(--cl-back);
	}

	.hard {
		margin: 10px;
		padding: 10px;
		color: var(--cl-font);
		border-style: solid;
		border-color: #5F4B28;
		border-width: 1px;
		border-radius: 4px;
		background-color: var(--cl-hard);
		font-size: 12px;
	}

	.contract {
		margin: 10px;

		.input {}

		.button {
			margin-top: 10px;
			margin-bottom: 10px;
			width: 100%;
			background-color: var(--cl-main);
			border-style: hidden;
			color: var(--cl-font);
		}

		.message {
			font-size: 12px;
		}

	}

	.dashboard {
		margin: 50px;
		display: flex;
		flex-direction: column;
		align-items: center;

		.bottom {
			font-size: 14px;
		}

		.search {
			margin-top: 8px;
			font-size: 12px;
			color: var(--cl-main);
		}
	}

	.community {
		margin: 10px;


		.table {
			margin: 20px 50px 0 50px
		}



		.total {
			width: 100%;
			text-align: center;
			font-size: 12px;
			color: var(--cl-main);
		}

		.remark {
			margin-top: 20px;
			font-size: 12px;
			color: var(--cl-main);
		}

	}

	.basics {
		margin-top: 20px;
		padding: 15px 8px;
		color: var(--cl-font);
		background-color: var(--cl-table);
		border-radius: 8px;
	}

	.table {
		margin-top: 10px;
		display: flex;
		font-size: 12px;
		justify-content: space-between;

		.left {
			display: flex;
			color: #767f8f
		}

		;

		.right {
			color: #878fbc;
		}

	}

	.logic {
		margin-top: 15px;
		display: flex;
		font-size: 12px;
		justify-content: space-between;

		.left {
			display: flex;
		}
	}

	.cash {
		display: flex;
		font-size: 12px;
		justify-content: space-between;
		padding: 5px 0;
	}
</style>
